<template>
  <div class="end-house">
    <!-- Q1 -->
    <div v-if="step===1" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-man-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>我們⋯⋯成功了嗎？</div>
          <div @click="goToStepQ2" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q2 -->
    <div v-if="step===2" class="house-0-box">
      <img style="opacity: 0.6;" class="house-0-big-fishbird" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>咳、咳，看來比起怪奇⋯⋯你們人類才是最可怕的存在呢。</div>
          <div @click="goToStepQ100" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q11 -->
    <div v-if="step===11" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>我們⋯⋯成功了嗎？</div>
          <div @click="goToStepQ12" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q12 -->
    <div v-if="step===12" class="house-0-box">
      <img style="opacity: 0.6;" class="house-0-big-fishbird" src="@/assets/images/ending/ending-man-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>咳、咳，你這個人類的叛徒，你不知道你做了什麼⋯⋯從此怪奇與人類將繼續無止境的戰爭，而你將再也不被視為是人類的一份子！</div>
          <div @click="goToStepQ100" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q21 -->
    <div v-if="step===21" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-man-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>可、可惡，失敗了嗎？為什麼！為什麼要阻止我？</div>
          <div @click="goToStepQ22" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q22 -->
    <div v-if="step===22" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>你既打倒館長，也耗盡了我的怪力⋯⋯你到底想怎麼樣？</div>
          <div @click="goToStepQ23" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q23 -->
    <div v-if="step===23" class="house-0-box">
      <div class="house-0-talk-box">
        <div class="house-0-head">你</div>
        <div class="house-0-frame">
          <div>你們都冷靜下來了嗎？我想清楚了，怪奇確實不該危害人類，但人類也沒有資格奪走怪奇的自由或是生命。因為我們都是會感覺、會思考的存在，我們一定有能力找到方法和平共處的！我不相信一方的存活，就得消滅掉另一方！你們⋯⋯我們一起努力吧！</div>
          <div @click="goToStepQ24" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q24 -->
    <div v-if="step===24" class="house-0-box">
      <div class="house-0-two-icon-box">
        <img class="house-0-two-icon" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
        <img class="house-0-two-icon" src="@/assets/images/ending/ending-man-big.png" alt="ghost">
      </div>
      <div class="house-0-talk-box">
        <div class="house-0-head-box">
          <div class="house-0-head">吉古拉</div>
          <div class="house-0-head">館長</div>
        </div>
        <div class="house-0-frame">
          <div>⋯⋯</div>
          <div @click="goToStepQ25" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q25 -->
    <div v-if="step===25" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-man-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>真是被你打敗了呀，你這種抱持理想的天真模樣，讓我想起了年輕時的我⋯⋯唉，既然如此，你自己想辦法善後吧。我不會再捕捉這些怪奇了，但只要有人類因怪奇受害，我絕對會出手阻止的！後會有期了。</div>
          <div @click="goToStepQ26" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q26 -->
    <div v-if="step===26" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>喂，你！你阻止我消滅館長，別以為我會感謝⋯⋯咦？你們說什麼？</div>
          <div @click="goToStepQ27" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q27 -->
    <div v-if="step===27" class="house-0-box">
      <div class="house-0-two-icon-box">
        <img class="house-0-monster-icon" src="@/assets/images/commute/passenger_colorful.png" alt="ghost">
        <img class="house-0-monster-icon" src="@/assets/images/ling/ling-color-album.png" alt="ghost">
        <img class="house-0-monster-icon" src="@/assets/images/river/river-color-icon.png" alt="ghost">
        <img class="house-0-monster-icon" src="@/assets/images/lulu/lulu-full-icon.png" alt="ghost"></div>
      <div class="house-0-talk-box">
        <div style="visibility: hidden;" class="house-0-head-box">
          <div class="house-0-head"></div>
          <div class="house-0-head"></div>
        </div>
        <div class="house-0-frame">
          <div>⋯⋯</div>
          <div @click="goToStepQ28" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q28 -->
    <div v-if="step===28" class="house-0-box">
      <img class="house-0-big-fishbird" src="@/assets/images/ending/ending-gg-big.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>你們看我幹嘛⋯⋯好、好啦，我說就是了，謝謝你救了我們啦。唉⋯⋯不過看來事情並沒有到此結束了，怪奇與人類，我們接下來到底該怎麼辦呢⋯⋯喂，人類。這是你做出的選擇，你得好好負起責任才行呀。</div>
          <div @click="goToStepQ100" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q100 -->
    <div v-if="step===100" class="house-0-box" :style="{ backgroundImage: 'url(' + monsterBg + ')' }">
      <div class="house-0-ending-box">
        <div v-html="who.text"></div>
        <div @click="goToVideo" v-html="who.btn" class="house-0-ending-btn">結局影片-怪奇與人類共處</div>
      </div>
    </div>
  </div>
</template>

<script>

import monsterBg from '@/assets/images/album-base.png'
export default {
  name: 'EndTalkTwo',
  components: {

  },
  data () {
    return {
      step: 1,
      monsterBg: monsterBg,
      ending: {
        man: {
          text: '你選擇了幫助館長<br>請點選連結以觀看結局',
          btn: '怪奇們的牢籠',
          link: 'https://youtu.be/82G3SC1scAo'
        },
        gg: {
          text: '你選擇了幫助吉古拉<br>請點選連結以觀看結局',
          btn: '怪奇自由了',
          link: 'https://youtu.be/tlzsWrdwDPU'
        },
        no: {
          text: '你選擇了爭鬥以外的第3條路<br>請點選連結以觀看結局',
          btn: '怪奇與人類共處',
          link: 'https://www.facebook.com/reborn.academy'
        }
      },
      who: ''
    }
  },
  mounted () {
    if (this.whoWin === 'man') {
      this.who = this.ending.man
      this.step = 1
    } else if (this.whoWin === 'gg') {
      this.who = this.ending.gg
      this.step = 11
    } else {
      this.who = this.ending.no
      this.step = 21
    }
  },
  methods: {
    goToStepQ2 () {
      this.step = 2
    },
    goToStepQ100 () {
      this.step = 100
      this.$store.commit('changeHeader', true)
      this.$store.commit('changeFrameDot', true)
    },
    goToStepQ12 () {
      this.step = 12
    },
    goToStepQ22 () {
      this.step = 22
    },
    goToStepQ23 () {
      this.step = 23
    },
    goToStepQ24 () {
      this.step = 24
    },
    goToStepQ25 () {
      this.step = 25
    },
    goToStepQ26 () {
      this.step = 26
    },
    goToStepQ27 () {
      this.step = 27
    },
    goToStepQ28 () {
      this.step = 28
    },
    goToVideo () {
      window.open(this.who.link)
    }
  },
  computed: {
    power () {
      return this.$store.state.power
    },
    whoWin () {
      return this.$store.state.whoWin
    }
  }
}
</script>
<style lang="scss" scoped>

  .end-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    .house-0 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-image: url('~@/assets/images/ending/ocean-plaza-bg.png');
      }

      &-fishbird {
        width: 24%;
        height: auto;
        position: absolute;
        top: 4vh;
      }

      &-talk-box {
        display: flex;
        flex-direction: column;
      }

      &-head {
        padding: 4px 16px;
        margin: 220px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      &-frame {
        width: 100vw;
        margin-top: 10px;
        max-height: 160px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      // black btn

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }

      // Q8

      &-big-fishbird {
        width: 100%;
        height: auto;
        position: absolute;
      }

      &-text {
        color: #95ff00;
        text-align: center;
      }

      &-all-box-quick {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 15vh;
        z-index: 1;
      }

      // Q245

      &-all-245-box {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 5vh;
        background-color: rgba(0, 0, 0, 0.8);
        color: white;
        z-index: 1;
      }

      &-245-fishbird {
        width: 30%;
        height: auto;
      }

      &-245-text {
      margin-top: 18vh;
      padding: 16px;
      }

      &-245-input {
        width: 150px;
        padding: 4px;
        color: black;
        margin-top: 10px;
      }

      &-245-go {
        background-color: #95ff00;
        color: black;
        padding: 6px;
        border-radius: 50%;
        text-align: center;
        margin-top: 10px;
      }

      &-last-frame {
        margin-top: 30vh;
        max-height: 200px;
        overflow: scroll;
        color: white;
        z-index: 1;
        padding: 20px;
      }

      // right

      &-right-talk-box {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }

      &-right-man {
        width: 100%;
        height: auto;
        position: absolute;
      }

      &-right-text {
        padding: 4px 16px;
        margin: 220px 50px 0px 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      // left

      &-left-talk-box {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }

      &-left-gg {
        width: 100%;
        height: auto;
        margin-left: -50vw;
        position: absolute;
      }

      &-left-text {
        padding: 4px 16px;
        margin: 220px 0px 0px 50px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        z-index: 1;
      }

      // light

      &-last-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-image: url('~@/assets/images/ending/ending-lighting.png');
      }

      &-last-text {
        color: #95ff00;
        padding: 10px;
        text-align: center;
      }

      // two icon

      &-two-icon-box {
        display: flex;
        align-items: center;
        position: absolute;
      }

      &-two-icon {
        width: 50%;
        height: auto;
      }

      &-head-box {
        display: flex;
        justify-content: center;
      }

      &-monster-icon {
        width: 26%;
        height: auto;
        margin: 0px -1vw 100px;
      }

      // ending

      &-ending-box {
        color: #95ff00;
        text-align: center;
        margin-top: 34vh;
        font-size: 18px;
      }

      &-ending-btn {
        font-size: 20px;
        background-color: #95ff00;
        border-radius: 20px;
        border: 10px;
        color: grey;
        width: 180px;
        height: 40px;
        line-height: 40px;
        margin: 20px auto 0px;
      }

    }

  }

</style>



